    >
      <div class="px-3 pb-3 pt-1">
        <div class="text-center mb-3" style="line-height: 1px">
          <h1 class="mb-2">Detail Education History</h1>
          <span>
            The last historical education will be used for further data
          </span>
        </div>
        <form @submit.prevent="edit_experience(experience)">
          <b-row class="d-none">
            <b-col md="6">
              <b-form-group label="* Company Name" label-for="company-name">
                <b-form-input
