<style>
.modal .modal-header {
  background: none !important;
}

.dropzone {
  border: 2px dashed #0561bd;
  border-radius: 0.25rem;
  background: #fff;
  min-height: 50px;
  padding: 0rem !important;
  text-align: center;
  color: #adb5bd;
  font-size: 1.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dropzone .dz-message {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0px;
  color: #adb5bd;
}
</style>

<script>
import Ripple from "vue-ripple-directive";
import flatPickr from "vue-flatpickr-component";
import AppTimeline from "@core/components/app-timeline/AppTimeline.vue";
import AppTimelineItem from "@core/components/app-timeline/AppTimelineItem.vue";
import axios from "axios";
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";

export default {
  components: {
    vueDropzone: vue2Dropzone,
    flatPickr,
    AppTimeline,
    AppTimelineItem,
    ToastificationContent,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      experiences: [],
      company_name: "",
      position: "",
      currently_working: false,
      start_date: "",
      end_date: "",
      end_currently_working: false,
      description: "",
      employment_type: "",
      location: "",
      job_title: "",
      media: [],

      dropzoneOptions: {
        url: "https://httpbin.org/post",
        thumbnailWidth: 150,
        maxFilesize: 6,
        headers: { "My-Awesome-Header": "header value" },
      },
    };
  },
  mounted() {
    this.callApi({
      url: "/career_experiences/fetch",
      method: "GET",
      params: {
        user_id: this.hashid(localStorage.getItem("detailEmp")),
      },
      success: (response) => {
        this.experiences = response.result;
      },
    });
  },
  computed: {},
  methods: {
    add_experience() {
      this.callApi({
        method: "POST",
        url: "/career_experiences/create",
        headers: {
          Authorization: `Bearer ${this.getCookie("sw_auth_token")}`,
        },
        data: {
          user_id: localStorage.getItem("detailEmp"),
          institution_name: this.institute_name,
          degree: this.degree,
          field_of_study: this.field_of_study,
          start_date: this.start_date,
          end_date: this.end_date,
          grade: this.grade,
          activities_societis: this.activities_societis,
        },
        success: (response) => {
          this.experiences.push(response.result);
          this.$bvModal.hide("add-education");
        },
      });
    },

    edit_education(education) {
      this.callApi({
        method: "POST",
        url: `/career_experiences/update/${this.hashid(education.id)}`,
        data: {
          user_id: localStorage.getItem("detailEmp"),
          institution_name: education.institution_name,
          degree: education.degree,
          field_of_study: education.field_of_study,
          start_date: education.start_date,
          end_date: education.end_date,
          grade: education.grade,
          activities_societis: education.activities_societis,
        },
        success: (response) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Success",
              text: "Education has been updated",
              icon: "CheckIcon",
              variant: "success",
            },
          });

          this.$bvModal.hide("update_" + education.id);
        },
        error: (error) => {
          console.log(error);
        },
      });
    },

    delete_education(id) {
      this.callApi({
        method: "DELETE",
        url: `/career_experiences/delete/${this.hashid(id)}`,
        headers: {
          Authorization: `Bearer ${localStorage.getItem("sw_auth_token")}`,
        },
      })
        .then((response) => {
          this.experiences = this.experiences.filter((education) => {
            return education.id != id;
          });
          this.$bvModal.hide("update_" + id);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
