<template>
  <b-card>
    <!-- media -->
    <b-media no-body>
      <b-media-aside>
        <b-link>
          <b-img
            rounded
            :src="avatar"
            height="80"
            @click="$refs.profileFile.$el.click()"
          />
        </b-link>
        <!--/ avatar -->
      </b-media-aside>

      <b-media-body class="mt-75 ml-75">
        <!-- upload button -->
        <b-button
          v-ripple.400="'rgba(255, 255, 255, 0.15)'"
          variant="primary"
          size="sm"
          class="mb-75 mr-75"
          @click="$refs.profileFile.$el.click()"
        >
          Upload
        </b-button>
        <b-form-file
          ref="profileFile"
          v-model="profileFile"
          accept=".jpg, .png, .gif"
          :hidden="true"
          plain
          @input="setPreview"
        />
        <!--/ upload button -->
        <b-card-text>Allowed JPG, GIF or PNG. Max size of 800kB</b-card-text>
      </b-media-body>
    </b-media>
    <!--/ media -->

    <!-- form -->
    <b-form class="mt-2" @submit.prevent="submitForm">
      <b-row>
        <b-col sm="6" md="4">
          <b-form-group label="* HR Code" label-for="hr-code">
            <b-form-input
              id="hr-code"
              v-model="hrcode"
              placeholder="HR Code"
              name="hr_code"
              readonly
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="* Fullname" label-for="fullname" required>
            <b-form-input
              id="fullname"
              v-model="name"
              placeholder="Fullname"
              name="fullname"
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Gender" label-for="gender">
            <v-select
              v-model="gender"
              :options="genderOptions"
              label="label"
              clearable
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Birthday" label-for="birthday">
            <flat-pickr
              v-model="birthday"
              :config="{
                dateFormat: 'Y-m-d',
                altInput: true,
                altFormat: 'F j, Y',
                allowInput: true,
              }"
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Birthplace" label-for="birthplace">
            <b-form-input
              id="birthplace"
              v-model="birthplace"
              placeholder="Birthplace"
              name="birthplace"
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Marital Status" label-for="marital_status">
            <v-select
              v-model="marital_status"
              :options="maritalStatusOptions"
              label="label"
              clearable
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Dependent" label-for="dependent">
            <v-select
              v-model="dependent"
              :options="dependentOptions"
              label="label"
              clearable
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Nationality" label-for="nationality">
            <v-select
              v-model="nationality"
              :options="nationalities"
              label="name"
              clearable
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Religion" label-for="religion">
            <v-select
              v-model="religion"
              :options="religions"
              label="name"
              clearable
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group
            label="Number Identification"
            label-for="number_identification"
          >
            <b-form-input
              id="number_identification"
              v-model="number_identification"
              placeholder="Number Identification"
              name="number_identification"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Place of Issue" label-for="place-of-issue">
            <b-form-input
              id="place-of-issue"
              v-model="place_of_issue"
              placeholder="Place of Issue"
              name="place_of_issue"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Branch" label-for="branch">
            <v-select
              v-model="branch"
              :options="branches"
              label="name"
              clearable
            />
          </b-form-group>
        </b-col>

        <b-col cols="12" class="text-right">
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mt-3"
            type="submit"
          >
            Save changes
          </b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-card>
</template>

<script>
import Ripple from "vue-ripple-directive";
import flatPickr from "vue-flatpickr-component";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import { json } from "body-parser";

export default {
  components: {
    flatPickr,
    ToastificationContent,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      profileFile: null,

      id: "",
      avatar: "",
      hrcode: "",
      name: "",
      gender: "",
      birthday: "",
      birthplace: "",
      marital_status: "",
      dependent: "",
      nationality: "",
      religion: "",
      number_identification: "",
      place_of_issue: "",
      branch: "",
      education_background: "",
      name_of_institution: "",
      email: "",

      nationalities: [
        {
          id: 1,
          name: "Indonesia",
        },
        {
          id: 2,
          name: "Singapura",
        },
        {
          id: 3,
          name: "Malaysia",
        },
      ],

      branches: [],

      religions: [
        {
          id: 1,
          name: "Islam",
        },
        {
          id: 2,
          name: "Kristen",
        },
        {
          id: 3,
          name: "Katolik",
        },
        {
          id: 4,
          name: "Hindu",
        },
        {
          id: 5,
          name: "Budha",
        },
        {
          id: 6,
          name: "Konghucu",
        },
      ],

      genderOptions: [
        { value: "MALE", label: "Male" },
        { value: "FEMALE", label: "Female" },
      ],

      maritalStatusOptions: [
        { value: "single", label: "Single" },
        { value: "married", label: "Married" },
        { value: "divorced", label: "Divorced" },
        { value: "widowed", label: "Widowed" },
      ],

      dependentOptions: [
        { value: "0", label: "Single" },
        { value: "1", label: "One Child" },
        { value: "2", label: "Two Child" },
        { value: "3", label: "Three Child" },
      ],
    };
  },

  created() {
    localStorage.setItem(
      "detailEmp",
      this.$route.params.id
        ? this.$route.params.id
        : localStorage.getItem("detailEmp")
    );
    this.callApi({
      method: "GET",
      url: "/users/getAll?id=" + this.hashid(localStorage.getItem("detailEmp")),
      success: (res) => {
        this.id = res.result.data[0].id;
        this.avatar = res.result.data[0].profile_photo_url;
        this.hrcode = res.result.data[0].hrcode;
        this.name = res.result.data[0].name;
        this.email = res.result.data[0].email;
        this.gender = res.result.data[0].gender;
        this.birthday = res.result.data[0].birthday;
        this.birthplace = res.result.data[0].birthplace;
        this.marital_status = res.result.data[0].marital_status;
        this.dependent = res.result.data[0].dependent;
        this.nationality = res.result.data[0].nationality;
        this.religion = res.result.data[0].religion;
        this.number_identification = res.result.data[0].number_of_identity;
        this.place_of_issue = res.result.data[0].place_of_identity;
        this.branch = res.result.data[0].branch;
      },
      error: (err) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            icon: "AlertTriangleIcon",
            variant: "danger",
            message: "Something went wrong!",
          },
        });
      },
    });

    this.callApi({
      method: "GET",
      url: "/branches/fetch",
      success: (res) => {
        this.branches = res.result.data;
      },
      error: (err) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            icon: "AlertTriangleIcon",
            variant: "danger",
            message: "Something went wrong!",
          },
        });
      },
    });
  },

  methods: {
    setPreview() {
      const file = this.$refs.profileFile.$el.files[0];
      const reader = new FileReader();
      reader.onload = (e) => {
        this.avatar = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    submitForm() {
      this.callApi({
        method: "POST",
        headers: {
          Authorization: "Bearer " + this.getCookie("sw_auth_token"),
        },
        url: "users/update/" + this.hashid(localStorage.getItem("detailEmp")),
        data: {
          name: this.name,
          gender: this.gender.value,
          birthday: this.birthday,
          birthplace: this.birthplace,
          marital_status: this.marital_status.value,
          dependent: this.dependent.value,
          nationality: this.nationality.name,
          religion: this.religion.name,
          number_of_identity: this.number_identification,
          place_of_identity: this.place_of_issue,
          branch: this.branch.code,
          team_id: 1,
          role_id: 1,
        },
      }).success((res) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Notification",
            icon: "BellIcon",
            text: "Success",
            variant: "success",
          },
        });
      });
    },
  },
};
</script>
