<template>
  <div>
    <form @submit.prevent="form_submit">
      <b-card sub-title="Job & Department">
        <b-row>
          <b-col md="6">
            <b-form-group label="* Department" label-for="department">
              <b-form-select id="department" v-model="department">
                <option
                  v-for="department in departments"
                  :key="department.id"
                  :label="department.name"
                  :value="department.id"
                ></option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group label="* Sub Department" label-for="sub-department">
              <b-form-select id="sub-department" v-model="sub_department">
                <option
                  v-for="subDepartment in subDepartments"
                  :key="subDepartment.id"
                  :label="subDepartment.name"
                  :value="subDepartment.id"
                ></option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group label="* Job Level" label-for="job-level">
              <b-form-select id="job-level" v-model="job_level">
                <option
                  v-for="jobLevel in jobLevels"
                  :key="jobLevel.id"
                  :label="jobLevel.name"
                  :value="jobLevel.id"
                ></option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group label="* Job Position" label-for="job-position">
              <b-form-select id="job-position" v-model="job_position">
                <option
                  v-for="jobPosition in jobPositions"
                  :key="jobPosition.id"
                  :label="jobPosition.name"
                  :value="jobPosition.id"
                ></option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group label="* Employee Type" label-for="employee-type">
              <b-form-select id="employee_type" v-model="employee_type">
                <option
                  v-for="employeeType in employeeTypes"
                  :key="employeeType.id"
                  :label="employeeType.name"
                  :value="employeeType.id"
                ></option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col cols="12" class="text-right mt-3">
            <b-button
              type="submit"
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              variant="primary"
              class="mr-1"
            >
              Save changes
            </b-button>
          </b-col>
        </b-row>
      </b-card>
    </form>
  </div>
</template>

<script>
import Ripple from "vue-ripple-directive";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";

export default {
  data() {
    return {
      departments: [],
      subDepartments: [],
      jobLevels: [],
      jobPositions: [],
      employeeTypes: [],
      department: "",
      sub_department: "",
      job_level: "",
      job_position: "",
      employee_type: "",
    };
  },
  directives: {
    Ripple,
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/departments/fetch",
      success: (response) => {
        this.departments = response.result.data;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
    this.callApi({
      method: "GET",
      url: "/departments/fetch",
      success: (response) => {
        this.subDepartments = response.result.data;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
    this.callApi({
      method: "GET",
      url: "/job_levels/fetch",
      success: (response) => {
        this.jobLevels = response.result.data;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
    this.callApi({
      method: "GET",
      url: "/teams/fetch",
      success: (response) => {
        this.jobPositions = response.result.data;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
    this.callApi({
      method: "GET",
      url: "/employee_types/fetch",
      success: (response) => {
        this.employeeTypes = response.result;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
    this.callApi({
      method: "GET",
      url: "/users/getAll",
      params: {
        id: this.hashid(localStorage.getItem("detailEmp")),
      },
      success: (response) => {
        response.result.data.forEach((element) => {
          this.department = element.department_id;
          this.sub_department = element.sub_department;
          this.job_level = element.job_level;
          this.job_position = element.team_id;
          this.employee_type = element.employee_type;
        });
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
  },
  computed: {},
  methods: {
    form_submit() {
      this.callApi({
        method: "POST",
        url: "/users/update/" + this.hashid(localStorage.getItem("detailEmp")),
        data: {
          department: this.department,
          sub_department: this.sub_department,
          job_level: this.job_level,
          job_position: this.job_position,
          employee_type: this.employee_type,
        },
        success: (response) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Success",
              message: "Job & Department data saved",
              type: "success",
            },
          });
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Error",
              message: "Job & Department data not saved",
              type: "error",
            },
          });
        },
      });
    },
  },
};
</script>
