<template>
  <div>
    <!-- <b-card> {{ jobData }} </b-card> -->
    <b-card>
      <form @submit.prevent="salary_form">
        <b-card sub-title="Sallary Information">
          <b-row>
            <b-col md="6">
              <b-form-group
                label="* Bank Account Number"
                label-for="bank-account-number"
              >
                <b-form-input
                  id="bank-account-number"
                  v-model="bank_account_number"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group
                label="* Name of Bank Account"
                label-for="name-of-bank-account"
              >
                <b-form-input
                  id="name-of-bank-account"
                  v-model="name_of_bank_account"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group label="* Bank of Issue" label-for="bank-os-issue">
                <b-form-input id="bank-os-issue" v-model="bank_of_issue" />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group label="* NPWP Number" label-for="npwp-number">
                <b-form-input id="npwp-number" v-model="npwp_number" />
              </b-form-group>
            </b-col>
            <b-col cols="12">
              <div class="text-right">
                <b-button type="submit" variant="primary">Save</b-button>
              </div>
            </b-col>
          </b-row>
        </b-card>
      </form>
      <hr />
      <b-row>
        <b-col md="6">
          <b-card sub-title="Wages">
            <b-form-group label="Since Date" label-for="since-date">
              <flat-pickr
                id="since-date"
                v-model="since_date"
                :config="{
                  dateFormat: 'Y-m-d',
                  altInput: true,
                  altFormat: 'F j, Y',
                  allowInput: true,
                }"
              />
            </b-form-group>
            <b-form-group label="Sallary Type" label-for="sallary-type">
              <b-form-select id="sallary-type" v-model="sallary_type">
                <option value="dailly">Dailly</option>
                <option value="monthly">Monthly</option>
              </b-form-select>
            </b-form-group>
            <b-row>
              <b-col md="6">
                <b-form-group label="Sallary Form" label-for="sallary-form">
                  <b-form-select id="sallary-form" v-model="sallary_form">
                    <option value="permanent">Permanent</option>
                    <option value="interenship">Interenship</option>
                  </b-form-select>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="Amount of Money"
                  label-for="amount-of-money"
                >
                  <b-form-input
                    type="number"
                    id="amount-of-money"
                    v-model="amount_of_money"
                  >
                  </b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
        <b-col md="6">
          <b-card sub-title="Allowances">
            <b-form-group label="Note" label-for="note">
              <b-form-textarea id="note" v-model="note" />
            </b-form-group>
            <b-row>
              <b-col md="6">
                <b-form-group label="Allowance Type" label-for="allowance-type">
                  <b-form-select id="allowance-type" v-model="allowances_type">
                    <option value="permanent">Permanent</option>
                    <option value="interenship">Interenship</option>
                  </b-form-select>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="Amount of Money"
                  label-for="amount-of-money"
                >
                  <b-form-input
                    type="number"
                    id="amount-of-money"
                    v-model="amount_of_money"
                  >
                  </b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
        <b-col cols="12" class="text-right">
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-1"
          >
            Save changes
          </b-button>
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<script>
import flatPickr from "vue-flatpickr-component";
import Ripple from "vue-ripple-directive";

export default {
  components: {
    flatPickr,
  },
  directives: {
    Ripple,
  },
  props: {},
  data() {
    return {
      bank_account_number: "",
      name_of_bank_account: "",
      bank_of_issue: "",
      npwp_number: "",
      since_date: "",
      sallary_type: "",
      sallary_form: "",
      amount_of_money: "",
      allowances_type: "",
      note: "",
    };
  },
  created() {
    this.callApi({
      url: "/salaries/fetch",
      method: "GET",
      params: {
        id: this.hashid(localStorage.getItem("detailEmp")),
      },
      success: (res) => {
        this.bank_account_number = res.data.bank_account_number;
        this.name_of_bank_account = res.data.name_of_bank_account;
        this.bank_of_issue = res.data.bank_of_issue;
        this.npwp_number = res.data.npwp_number;
        this.since_date = res.data.since_date;
        this.sallary_type = res.data.sallary_type;
        this.sallary_form = res.data.sallary_form;
        this.amount_of_money = res.data.amount_of_money;
        this.allowances_type = res.data.allowances_type;
        this.note = res.data.note;
      },
    });
  },
  computed: {},
  methods: {
    salary_form() {
      this.callApi({
        url: "/salaries/create",
        method: "POST",
        data: {
          user_id: localStorage.getItem("detailEmp"),
          bank_account_number: this.bank_account_number,
          bank_name: this.name_of_bank_account,
          bank_of_issue: this.bank_of_issue,
          npwp_number: this.npwp_number,
        },
        success: (res) => {},
      });
    },
  },
};
</script>
